.content {
    display: flex;
    align-items: center;
    padding: 5rem 0.5rem;
    color: #fff;
  }
  
  .content h1 {
    font-size: 45px;
    font-weight: 900;
    line-height: 34.72px;
    margin-bottom: 30px;
    padding-right: 50px;
    padding-left: 50px;
  
  }
  
  .content p {
    margin-bottom: 2rem;
    line-height: 20px;
    font-size: 13px;
    font-weight: 500;
    letter-spacing: 1.8%;
    padding-right: 50px;
    padding-left: 50px;
  }
  
  #error-para {
    margin-bottom: 0rem;
    color: #f00000;
  }
  
  .dropdown {
    font-weight: bold;
    font-size: 12px;
    color: #74747c;
    padding: 0.8rem 1rem;
    border-radius: 2px;
    border: 1px solid #cfcccc;
    width: 100%;
  }
  
  .tag {
    background-color: #f00000;
    padding: 0.8rem 1rem;
    border-radius: 5px;
    box-shadow: 0px 4px 4px 0px #0000005C;
    font-size: 10px;
    display: flex;
    justify-content: center;
  }
  
  .form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: #fff;
    padding: 2rem;
    border-radius: 7px;
    box-shadow: 0px 4px 4px 0px #00000040;
    padding-bottom: 2rem;
  }
  
  .form input {
    font-weight: bold;
    font-size: 12px;
    color: #000;
    padding: 0.8rem 1rem;
    border-radius: 2px;
    border: 1px solid #dadada;
  }
  
  .form p {
    margin-bottom: 1rem;
  }
  
  .buttons-container {
    display: flex;
    justify-content: center;
  }
  
  
  #button {
    background-color: #37CC8A;
    font-weight: bold;
    color: #fff;
    padding: 0.8rem 2rem;
    border: none;
    border-radius: 2px;
    cursor: pointer;
    letter-spacing: 0.1rem;
    transition: all .2s ease-in-out;
    box-shadow: 0px 1px 0px 0px #00000075;
  }
  
  #secondary-button {
    height: 17px;
    background-color: #dcdcdc;
    border: transparent;
    color: #777777;
    border-radius: 8px;
    width: 58px;
    font-size: 9px;
    margin-right: 9px;
  }
  
  #button:hover {
    background-color: #3eac73;
    transform: scale(1.1);
  }
  
  .form small {
    color: #c2c2c2;
    text-align: center;
  }
  
  .form small span {
    color: #ff0000;
    font-weight: bold;
    cursor: pointer;
  
  }
  
  .finalMsg {
    color: #fff;
    font-size: 45px;
    font-weight: 600;
    display: grid;
    justify-content: center;
  }
  
  @media screen and (max-width: 768px) {
  
    /* CSS for screens with a maximum width of 768px */
    .content {
      flex-direction: column;
      justify-content: center;
      text-align: center;
  
    }
  
    .content h1 {
      font-size: 45px;
      font-weight: 900;
      line-height: 34.72px;
      margin-bottom: 30px;
      padding-left: 10px;
    }
  
    .content p {
      font-weight: 500;
      font-size: 11px;
      line-height: 99.19%;
      letter-spacing: 0.018em;
    }
  
    .form {
      padding: 1rem;
    }
  
    #button {
      padding: 0.5rem 1rem;
    }
  }
  @media screen and (max-width: 480px) {
  
    /* CSS for screens with a maximum width of 480px */
    .content h1 {
      font-size: 2rem;
    }
  
    .form input {
      font-size: 1rem;
    }
    .dropdown{
      font-size: 1rem;
    }
  
    #button {
      font-size: 1.2rem;
    }
  }